import {css} from "styled-components";

const BREAKPOINTS = [1024];

//    0,     BREAKPOINTS 
export function adaptiveProps(
    property: string,
    mobileSize?: string,
    desktopSize?: string,
) {
    const result = css`
        ${property} : ${mobileSize};

        //        
        ${[desktopSize].map(
            (value, index) =>
                css`
                    @media (min-width: ${BREAKPOINTS[index]}px) {
                        ${property} : ${value}
                    }
                `,
        )}
    `;

    return result;
}
